import {List, SimpleListItem} from '@rmwc/list'
import '@rmwc/list/styles';
import { useEffect } from 'react';
import moment from 'moment'

function ListContainer( {schedule, info, periods, day, setMax, setIndex, index} ){
    useEffect(() => setIndex(day === 0 || day === 6 ? 0 : day - 1), [])
    setMax(4);
    
    
    var now = moment()
    useEffect(() => setInterval(() => now = moment(), 1000))
    return (
        <List twoLine>
            {
                
                schedule[index + 1].map((a, b) => {
                    var classStart = moment(periods[b].split('-')[0],'hhmm');
                    var classEnd = moment(periods[b].split('-')[1],'hhmm');
                    
                    return (
                        <SimpleListItem
                            graphic={info[a].icon}
                            text={a}
                            secondaryText={periods[b]}
                            selected={now.isBefore(classEnd) && classStart.subtract(10, 'minutes').isBefore(now)}
                            
                        />
                    )
                })
            }

        </List>
    )
}

export default ListContainer