import '@rmwc/top-app-bar/styles';
import {SimpleTopAppBar, TopAppBarFixedAdjust} from '@rmwc/top-app-bar'
import {Drawer, DrawerHeader, DrawerTitle, DrawerSubtitle, DrawerContent} from '@rmwc/drawer'
import {List, ListItem} from '@rmwc/list'
import React from 'react';
import '@rmwc/drawer/styles';
import '@rmwc/list/styles';
function AppBar({page, setPage, max, index, setIndex}){
    const [open, setOpen] = React.useState(false)

    function handleBefore(evt){
      setIndex(a => a - 1)
    }

    function handleNext(evt){
      setIndex(a => a + 1)
    }

    return (
        <>
        <SimpleTopAppBar
    title={page}
    navigationIcon
    onNav={() => setOpen(true)}
    actionItems={ [
      { icon: 'navigate_before', onClick: handleBefore, disabled: (index === 0) },
      { icon: 'navigate_next', onClick: handleNext, disabled: (index === max) }
    ]}

  />
  <TopAppBarFixedAdjust />
  <Drawer modal open={open} onClose={() => setOpen(false)}>

        <DrawerContent>
          <List wrapFocus={false}>
            <ListItem onClick={() => setPage("Schedule")} selected={page === "Schedule"}>Schedule</ListItem>
            <ListItem onClick={() => {setPage("Todos")}} selected={page === "Todos"}>Todos</ListItem>
          </List>
        </DrawerContent>
      </Drawer>
        </>
    )
}
export default AppBar

